export default {
	name:"tuiAlert",
	props: {
		//控制显示
		show: {
			type: Boolean,
			default: false
		},
		//提示信息字体大小
		size: {
			type: Number,
			default: 30
		},
		//提示信息字体颜色
		color: {
			type: String,
			default: "#333"
		},
		//按钮字体颜色
		btnColor: {
			type: String,
			default: "#EB0909"
		},
		btnText:{
			type: String,
			default: "确定"
		},
		//点击遮罩 是否可关闭
		maskClosable: {
			type: Boolean,
			default: false
		}
	},
	methods: {
		handleClick(e) {
			if (!this.show) return;
			this.$emit('click', {});
		},
		handleClickCancel() {
			if (!this.maskClosable) return;
			this.$emit('cancel');
		}
	}
}
